.footer {
    background: #201e1c;
    font-size: 15px;
}

footer .socials i {
    padding: 10px 20px 0 0;
    font-size: 40px
}

footer .socials i:hover {
    color: var(--three)
}

.credits h5 {
    margin: 20px 0;
    color: #fff;
    font-family: 'Roboto Condensed', sans-serif;
}
.credits li {
    list-style-type: none;
    color: var(--one);
    font-family: 'Comfortaa', cursive;
    font-size: 15px;
    text-align: center
}
.credits li:hover {
    color: #8d8d8d;
    cursor: pointer;
}
.credits a {
    text-decoration: none;
}
.credits img{
    border-radius: 50%;
    padding: 5px;
    border: 2px solid rgba(255, 255, 255, 0.281);
    width: 40%;
    margin: 0 0 10px 0;
}

.copyright {
    background-color: #0e0f0f;
    color: #8d8d8d;
    text-align: center;
    padding: 25px 0 15px 0;
    font-family: 'Comfortaa', cursive;
    font-size: small
}

.fab {
    color: #ffffff;
}

.footer-wave{
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
}

@media only screen and (max-width: 800px) {
    .footer {
        padding: 0 30px;
        text-align: center
    }
    .footer a {
        margin-top: 30px
    }
    .credits {
        margin: 30px auto 0px auto
    }
    .credits ul {
        padding: 0!important
    }
    .footer .footDesc {
        text-align: center!important
    }
  }
  @media only screen and (min-width: 767px) {
    .footer .credits {
        text-align: right
    }
    .footDesc {
        width: 50%
    }
}

.cFooterlink:hover {
    cursor: pointer;
}

.contribCard {
    border-radius: 5px 5px 10px 10px;
    border: 1px solid rgba(80, 80, 80, 0.322);
    margin: 10px;
    background-color: rgba(255, 255, 255, 0.664);
}

.contribCard:hover {
    box-shadow: 4px 4px 16px 0px rgba(0, 0, 0, 0.22);
}

.contribCard>p {
    color: rgba(97, 97, 97, 0.589);
    font-size: 12px;
    margin-top: 0px
}

.contribCard h5 {
    color: var(--three);
    text-transform: uppercase;
    margin: 20px 0 10px 0
}

.contribCard img {
    border-radius: 5px 5px 0 0;
    width: 100%;
}

.modal-header h5 {
    margin: 0px;
    color: var(--three)
}

.modal-body {
    background: url(/static/media/parallax.4cbbd78c.svg);
    margin: 0;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.modal-body .courseSubTitle {
    color: var(--one)
}
.courseCard {
    border-radius: 5px 5px 10px 10px;
    border: 1px solid rgba(80, 80, 80, 0.322);
    margin: 10px;
    background-color: rgba(255, 255, 255, 0.664);
}

.courseCard:hover {
    box-shadow: 4px 4px 16px 0px rgba(0, 0, 0, 0.22);
}

.courseCard p {
    color: rgba(121, 121, 121, 0.589);
    font-size: 14px;
    text-align: left;
    padding: 0 15px;
}

.courseCard h5 {
    font-size: 20px;
    color: var(--three);
    text-transform: uppercase;
    margin: 20px 0;
    text-align: center;
}

.courseCard img {
    border-radius: 5px 5px 0 0;
    width: 100%;
    padding: 0 0 20px 20px;
}

.modal-header h5 {
    margin: 0px;
    color: var(--three)
}

.modal-body {
    background: url(/static/media/parallax.4cbbd78c.svg);
    margin: 0;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
